<template>
    <div class="table">
        <div>
            <el-table
                :data="tableData.slice((currpage - 1) * pagesize, currpage * pagesize)"
                border
                style="width: 100%">
                <el-table-column
                label="姓名"
                width="180">
                <template slot-scope="scope">
                    <span>{{ scope.row.name }}</span>
                </template>
                </el-table-column>
                <el-table-column
                label="性别"
                width="180">
                <template slot-scope="scope">
                    <span>{{ scope.row.gender == 1 ? "男":"女" }}</span>
                </template>
                </el-table-column>
                <el-table-column
                label="微信号"
                width="180">
                <template slot-scope="scope">
                    <span>{{ scope.row.wx_num }}</span>
                </template>
                </el-table-column>
                <el-table-column
                label="手机号"
                width="180">
                <template slot-scope="scope">
                    <span>{{ scope.row.mobile }}</span>
                </template>
                </el-table-column>
                <el-table-column
                label="邮箱"
                width="180">
                <template slot-scope="scope">
                    <span>{{ scope.row.email }}</span>
                </template>
                </el-table-column>
                <el-table-column
                label="密码"
                width="180">
                <template slot-scope="scope">
                    <span>{{ scope.row.password }}</span>
                </template>
                </el-table-column>
                <el-table-column
                label="等级"
                width="180">
                <template slot-scope="scope">
                    <span>{{ scope.row.dj }}</span>
                </template>
                </el-table-column>
                <el-table-column
                label="部门"
                width="180">
                <template slot-scope="scope">
                    <span>{{ scope.row.dept_name }}</span>
                </template>
                </el-table-column>
                <el-table-column
                label="职务"
                width="180">
                <template slot-scope="scope">
                    <span>{{ scope.row.zw }}</span>
                </template>
                </el-table-column>
                <el-table-column
                label="是否为负责人"
                width="180">
                <template slot-scope="scope">
                    <span>{{ scope.row.status == 1 ? "是":"否" }}</span>
                </template>
                </el-table-column>
                <el-table-column label="操作" width="180">
                <template slot-scope="scope">
                    <el-button
                    size="mini"
                    @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
                    <el-button
                    size="mini"
                    type="danger"
                    @click="handleDelete(scope.$index, scope.row)">删除</el-button>
                </template>
                </el-table-column>
            </el-table>
        </div>
        <div class="block">
            <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="currentPage"
            :page-sizes="[2, 3, 5, 10]"
            :page-size="pagesize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="total">
            </el-pagination>
        </div>
        <div class="mask1" ref="mask">
            <div class="bianji">
                <ul>
                    <li>
                        姓名：<br/>
                        <el-input class="elinput"
                            placeholder="请输入内容"
                            v-model="input11"
                            ref="xname"
                            clearable>
                        </el-input>
                    </li>
                    <li>
                        性别：<br/>
                        <template>
                        <el-select class="elinput" @change="xb" v-model="value81" filterable placeholder="请选择">
                            <el-option
                            v-for="item in options81"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value">
                            </el-option>
                        </el-select>
                        </template>
                    </li>
                    <li>
                        微信号：<br/>
                        <el-input class="elinput"
                            placeholder="请输入内容"
                            v-model="input12"
                            ref="wwx"
                            clearable>
                        </el-input>
                    </li>
                    <li>
                        手机号：<br/>
                        <el-input class="elinput"
                            placeholder="请输入内容"
                            v-model="input13"
                            ref="ssj"
                            clearable>
                        </el-input>
                    </li>
                    <li>
                        邮箱：<br/>
                        <el-input class="elinput"
                            placeholder="请输入内容"
                            v-model="input14"
                            ref="yyx"
                            clearable>
                        </el-input>
                    </li>
                    <li>
                        密码：<br/>
                        <el-input class="elinput"
                            placeholder="请输入内容"
                            v-model="input15"
                            ref="mmm"
                            clearable>
                        </el-input>
                    </li>
                    <li>
                        等级：<br/>
                        <template>
                        <el-select class="elinput" @change="dj" v-model="value82" filterable placeholder="请选择">
                            <el-option
                            v-for="item in options82"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value">
                            </el-option>
                        </el-select>
                        </template>
                    </li>
                    <li>
                        部门：<br/>
                        <template>
                        <el-select class="elinput" @change="bm" v-model="value83" filterable placeholder="请选择">
                            <el-option
                            v-for="item in options83"
                            :key="item.id"
                            :label="item.dept_name"
                            :value="item.id">
                            </el-option>
                        </el-select>
                        </template>
                    </li>
                    <li>
                        职务：<br/>
                        <el-input class="elinput"
                            placeholder="请输入内容"
                            v-model="input16"
                            ref="zzw"
                            clearable>
                        </el-input>
                    </li>
                    <li>
                        是否为负责人：<br/>
                        <template>
                        <el-select class="elinput" @change="fz" v-model="value84" filterable placeholder="请选择">
                            <el-option
                            v-for="item in options84"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value">
                            </el-option>
                        </el-select>
                        </template>
                    </li>
                </ul>
                <el-row class="row">
                    <el-button @click="quxiao">取消</el-button>
                    <el-button type="primary" @click="bqr">确认</el-button>
                </el-row>
            </div>
        </div>
        <div class="mask2" ref="mask2">
            <div class="bianji2">
                <p class="place2">您确认删除此条信息吗？</p>
                <el-row class="row2">
                    <el-button @click="quxiao2">取消</el-button>
                    <el-button type="primary" @click="sqr">确认</el-button>
                </el-row>
            </div>
        </div>
        <i class="el-icon-plus outline" @click="tianjia"></i>
        <div class="tmask" ref="tmask">
            <div class="tianjia">
                <ul>
                    <li>
                        姓名：<br/>
                        <el-input class="elinput"
                            placeholder="请输入内容"
                            v-model="input31"
                            ref="xname"
                            clearable>
                        </el-input>
                    </li>
                    <li>
                        性别：<br/>
                        <template>
                        <el-select class="elinput" @change="xb" v-model="value81" filterable placeholder="请选择">
                            <el-option
                            v-for="item in options81"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value">
                            </el-option>
                        </el-select>
                        </template>
                    </li>
                    <li>
                        微信号：<br/>
                        <el-input class="elinput"
                            placeholder="请输入内容"
                            v-model="input32"
                            ref="wwx"
                            clearable>
                        </el-input>
                    </li>
                    <li>
                        手机号：<br/>
                        <el-input class="elinput"
                            placeholder="请输入内容"
                            v-model="input33"
                            ref="ssj"
                            clearable>
                        </el-input>
                    </li>
                    <li>
                        邮箱：<br/>
                        <el-input class="elinput"
                            placeholder="请输入内容"
                            v-model="input34"
                            ref="yyx"
                            clearable>
                        </el-input>
                    </li>
                    <li>
                        密码：<br/>
                        <el-input class="elinput"
                            placeholder="请输入内容"
                            v-model="input35"
                            ref="mmm"
                            clearable>
                        </el-input>
                    </li>
                    <li>
                        等级：<br/>
                        <template>
                        <el-select class="elinput" @change="dj" v-model="value82" filterable placeholder="请选择">
                            <el-option
                            v-for="item in options82"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value">
                            </el-option>
                        </el-select>
                        </template>
                    </li>
                    <li>
                        部门：<br/>
                        <template>
                        <el-select class="elinput" @change="bm" v-model="value83" filterable placeholder="请选择">
                            <el-option
                            v-for="item in options83"
                            :key="item.id"
                            :label="item.dept_name"
                            :value="item.id">
                            </el-option>
                        </el-select>
                        </template>
                    </li>
                    <li>
                        职务：<br/>
                        <el-input class="elinput"
                            placeholder="请输入内容"
                            v-model="input36"
                            ref="zzw"
                            clearable>
                        </el-input>
                    </li>
                    <li>
                        是否为负责人：<br/>
                        <template>
                        <el-select class="elinput" @change="fz" v-model="value84" filterable placeholder="请选择">
                            <el-option
                            v-for="item in options84"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value">
                            </el-option>
                        </el-select>
                        </template>
                    </li>
                </ul>
                <el-row class="row">
                    <el-button @click="tquxiao">取消</el-button>
                    <el-button type="primary" @click="tqr">确认</el-button>
                </el-row>
            </div>
        </div>
    </div>
</template>

<script>
import axios from 'axios';
export default {
    name:"renyuan",
    inject:["reload"],
    data(){
        return{
            aaa:true,
            currentPage: 1,
            total:100,
            pagesize:10,
            currpage:1,
            input10:"",
            input11:"",
            input12:"",
            input13:"",
            input14:"",
            input15:"",
            input16:"",
            input31:"",
            input32:"",
            input33:"",
            input34:"",
            input35:"",
            input36:"",
            tableData: [],
            options81: [{
            value: '1',
            label: '男'
            }, {
            value: '2',
            label: '女'
            }],
            value81: '',
            options82: [{
            value: '1',
            label: '1'
            }, {
            value: '2',
            label: '2'
            },{
            value: '3',
            label: '3'
            }, {
            value: '4',
            label: '4'
            }],
            value82: '',
            options83: [],
            value83: '',
            options84: [{
            value: '0',
            label: '否'
            }, {
            value: '1',
            label: '是'
            }],
            value84: ''
        }
    },
    methods: {
      quxiao(e){
          this.$refs.mask.style.display = "none";
      },
      quxiao2(e){
          this.$refs.mask2.style.display = "none";
      },
      tianjia(e){
          this.$refs.tmask.style.display = "block";
      },
      tquxiao(e){
          this.$refs.tmask.style.display = "none";
      },
      xb(e){
          console.log(e)
          localStorage.setItem("xb",e)
      },
      dj(e){
          console.log(e)
          localStorage.setItem("dj",e)
      },
      bm(e){
          console.log(e)
          localStorage.setItem("bmid",e)
      },
      fz(e){
          console.log(e)
          localStorage.setItem("fz",e)
      },
      handleEdit(index, row) {
        console.log(index, row);
        this.input11 = row.name
        this.input12 = row.wx_num
        this.input13 = row.mobile
        this.input14 = row.email
        this.input15 = row.password
        this.input16 = row.zw
        this.value81 = row.gender
        this.value82 = row.dj
        this.value84 = row.status
        var that = this;
        axios.post(this.baseUrl+'/ydjxzg/ht/bumenlist.action')
            .then(function (res) {
                console.log(res);
                that.options83 = res.data.bumen;
            })
        localStorage.setItem("bid",row.user_id)
        this.$refs.mask.style.display = "block";
      },
      handleDelete(index, row) {
        console.log(index, row);
        localStorage.setItem("sid",row.user_id)
        this.$refs.mask2.style.display = "block";
      },
      handleSizeChange(val) {
        console.log(`每页 ${val} 条`);
        this.pagesize = val;
      },
      handleCurrentChange(val) {
        console.log(`当前页: ${val}`);
        this.currpage = val;
      },
      bqr(){
        var that = this;
        var name = this.$refs.xname.value;
        var mmm = this.$refs.mmm.value;
        var xb = localStorage.getItem("xb");
        var ssj = this.$refs.ssj.value;
        var yyx = this.$refs.yyx.value;
        var wwx = this.$refs.wwx.value;
        var dj = localStorage.getItem("dj");
        var zzw = this.$refs.zzw.value;
        var fz = localStorage.getItem("fz");
        var bid = localStorage.getItem("bid");
        var bmid = localStorage.getItem("bmid");
        let formData = new FormData();
        formData.append('name',name);
        formData.append('password',mmm);
        formData.append('gender',xb);
        formData.append('mobile',ssj);
        formData.append('email',yyx);
        formData.append('wx_num',wwx);
        formData.append('dj',dj);
        formData.append('dept_id',bmid);
        formData.append('zw',zzw);
        formData.append('status',fz);
        formData.append('user_id',bid);
        axios.post(this.baseUrl+'/ydjxzg/ht/updateUser.action',formData)
            .then(function (res) {
                console.log(res);
                if(res.data.mess == true){
                    that.$message.success('编辑成功')
                    that.reload()
                }else if(res.data.mess == false){
                    that.$message.error('编辑失败')
                }else{
                    that.$message.error(res.data.mess)
                }
            })
      },
      sqr(){
          var that = this;
          var sid = localStorage.getItem("sid");
          let formData = new FormData();
          formData.append('id',sid);
            axios.post(this.baseUrl+'/ydjxzg/ht/deleteUser.action',formData)
                .then(function (res) {
                    console.log(res);
                    if(res.data.mess == true){
                        that.$message.success('删除成功')
                        that.reload()
                    }else if(res.data.mess == false){
                        that.$message.error('删除失败')
                    }
                })
      },
      tqr(){
          var that = this;
        var name = this.$refs.xname.value;
        var mmm = this.$refs.mmm.value;
        var xb = localStorage.getItem("xb");
        var ssj = this.$refs.ssj.value;
        var yyx = this.$refs.yyx.value;
        var wwx = this.$refs.wwx.value;
        var dj = localStorage.getItem("dj");
        var zzw = this.$refs.zzw.value;
        var fz = localStorage.getItem("fz");
        var bid = localStorage.getItem("bid");
        var bmid = localStorage.getItem("bmid");
        let formData = new FormData();
        formData.append('name',name);
        formData.append('password',mmm);
        formData.append('gender',xb);
        formData.append('mobile',ssj);
        formData.append('email',yyx);
        formData.append('wx_num',wwx);
        formData.append('dj',dj);
        formData.append('dept_id',bmid);
        formData.append('qq_num',2124154545);
        formData.append('zw',zzw);
        formData.append('status',fz);
        formData.append('user_id',bid);
        if(name == "" || name == null){
            that.$message.error('姓名不能为空')
        }else if(mmm == "" || mmm == null){
            that.$message.error('密码不能为空')
        }else if(ssj == "" || ssj == null){
            that.$message.error('手机号不能为空')
        }else if(yyx == "" || yyx == null){
            that.$message.error('邮箱不能为空')
        }else if(wwx == "" || wwx == null){
            that.$message.error('微信号不能为空')
        }else if(zzw == "" || zzw == null){
            that.$message.error('职务不能为空')
        }else{
            axios.post(this.baseUrl+'/ydjxzg/ht/insertUser.action',formData)
            .then(function (res) {
                console.log(res);
                that.$message.success('添加成功')
                that.reload()
            })
        }
      }
    },
    mounted(){
        var that = this;
        axios.post(this.baseUrl+'/ydjxzg/ht/getuserlist.action')
            .then(function (res) {
                console.log(res);
                that.tableData = res.data.userlist;
                that.total = res.data.userlist.length;
            });
        axios.post(this.baseUrl+'/ydjxzg/ht/bumenlist.action')
            .then(function (res) {
                console.log(res);
                that.options83 = res.data.bumen;
            })
    }
}
</script>

<style scoped>
.table{
    margin: 50px 0 0 50px;
    width: 80%;
    height: 80%;
}
.block{
    margin-top: 30px;
    float: right;
}
.mask1{
    position: fixed;
    top: 0;
    left: 0;
    z-index: 3;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    display: none;
}
.bianji{
    position: absolute;
    width: 1000px;
    height: 800px;
    background-color: #ffffff;
    top: 50%;
    left: 50%;
    margin-top: -400px;
    margin-left: -500px;
}
.bianji ul{
    padding: 15px 0 0 180px;
}
.bianji ul li{
    margin-top: 10px;
}
.elinput{
    width: 60%;
    margin-left: 120px;
}
.row{
    float: right;
    margin: 15px 30px 0 0 ;
}
.mask2{
    position: fixed;
    top: 0;
    left: 0;
    z-index: 3;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    display: none;
}
.bianji2{
    position: absolute;
    width: 400px;
    height: 180px;
    background-color: #ffffff;
    top: 50%;
    left: 50%;
    margin-top: -90px;
    margin-left: -200px;
}
.place2{
    font-size: 18px;
    text-align: center;
    margin-top: 30px;
}
.row2{
    text-align: center;
    margin-top: 50px; 
}
.outline{
    position: fixed;
    right: 0;
    bottom: 0;
    margin-right: 60px;
    margin-bottom: 60px;
    font-size: 54px;
    background-color: red;
    padding: 5px;
    color: #ffffff;
    border-radius: 50%;
    box-shadow: 5px 5px 5px 5px #cccccc;
}
.tmask{
    position: fixed;
    top: 0;
    left: 0;
    z-index: 3;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    display: none;
}
.tianjia{
    position: absolute;
    width: 1000px;
    height: 800px;
    background-color: #ffffff;
    top: 50%;
    left: 50%;
    margin-top: -400px;
    margin-left: -500px;
}
.tianjia ul{
    padding: 15px 0 0 180px;
}
.tianjia ul li{
    margin-top: 10px;
}
</style>



